import latelyShopList from '../component/latelyShopList';
import latelyPhotoList from '../component/latelyPhotoList';
import foot from '../component/foot';
import pop from '../component/pop';
import {
	mapMutations
} from 'vuex'
export default {
	components: {
		latelyShopList,
		latelyPhotoList,
		foot,
		pop,
	},
	data() {
		return {
			topLeftText: "我的个人中心",
			latelyList: [],
			photoList: [],
			userInfo: {},
			popCont: "您今天对此条留言的点赞次数已达上限",
			isShowSafearea: false,
			
		}
	},
	watch: {
		photoList: {
			handler(newVal, oldVal) {
				for (let i = 0; i < newVal.length; i++) {
					if (oldVal[i] != newVal[i]) {
						this.photoList = newVal;
					}
				}
			},
			deep: true
		}
	},
	created() {
		this.getUserInfo();
	},
	mounted() {
		// this.isShowSafearea = this.$common.checkPlatFromFunc();
	},
	methods: {
		getUserInfo() {
			this.$http.getUserInfo({}, res => {
				if (res.code == 1) {
					this.userInfo = res.data.userinfo;
					this.postCommentShop();
					this.postBeenShop();
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
		},
		postCommentShop() {
			this.$http.postCommentShop({
				user_id: this.userInfo.user_id
			}, res => {
				if (res.code == 1) {
					this.photoList = res.data.slice(0,1);
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
		},
		postBeenShop() {
			this.$http.postBeenShop({
				user_id: this.userInfo.user_id
			}, res => {
				if (res.code == 1) {
					this.latelyList = res.data;
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
		},
		previewImage(v, e) {
			const current = e.currentTarget.dataset.src;
			uni.previewImage({
				urls: v,
				current
			})
		},
		_changeLike(val) {
			const {
				item,
				bl,
				index
			} = val;
			let num = +this.photoList[index].zan;

			if (bl) {
				this.$http.postSaveZan({
					cid: item.id,
					uid: item.uid
				}, res => {
					if (res.code == 1) {
						this.$set(this.photoList[index], `like`, bl);
						this.$set(this.photoList[index], `zan`, ++num);
					} else {
						this.$common.errorToShow(res.msg);
					}
				})
			} else {
				// if (num > 0) {
				// 	this.photoList[index].zan = num - 1;
				// }
				this.$refs.popup.$refs.pop.open();
			}
		},
		_changeFullText(val) {
			const {
				e
			} = val;
			const index = e.currentTarget.dataset.index;
			const str = e.currentTarget.dataset.text;
			this.photoList[index].full_text = str == "全文" ? "收起全文" : "全文";
			
		},
		_switchToMsg() {
			uni.navigateTo({
				url: "/pages/user/msg"
			})
		},
		_switchEditInfo() {
			uni.navigateTo({
				url: "/pages/user/info"
			})
		},
		_switchlatelyPhoto() {
			uni.navigateTo({
				url: `/pages/user/latelyPhoto?total=${this.userInfo.commentCount}`
			})
		},
		_switchlatelyShop() {
			uni.navigateTo({
				url: `/pages/user/latelyShop?total=${this.userInfo.beenCount}`
			})
		},

		_switchShopHome(val) {
			this.$emit("handleClick", val)

			// const id = item.id

			// uni.navigateTo({
			// 	url: "/pages/home/home?page=shop",
			// })
		}
	}
}
